import classnames from 'classnames';
import React, { useEffect } from 'react';
import { KEY, Master, Organization, StreamAlignedTeam } from '../../types';
import { Tooltip } from '../Atoms/Tooltip';
import { Member } from '../Molecules/Member';
interface Props extends React.HTMLAttributes<HTMLElement> {
  organization: Organization;
  master: Master;
}
export const MemberCharts: React.FC<Props> = (props) => {
  const { organization, master } = props;
  const { streamAlignedTeams } = master;
  const [teamCommits, setTeamCommits] =
    React.useState<{ [key: StreamAlignedTeam['key']]: number; sum: number }>();

  useEffect(() => {
    setTeamCommits(
      Object.keys(master.streamAlignedTeams).reduce(
        (sum, streamAlignedTeamKey) => {
          sum[streamAlignedTeamKey] = Object.keys(
            organization[streamAlignedTeamKey]
          )
            .map((tag) => organization[streamAlignedTeamKey][tag])
            .flat()
            .filter((m) => m.streamAlignedTeam[KEY] === streamAlignedTeamKey)
            .reduce((streamAlignedTeamSum, member) => {
              streamAlignedTeamSum += member.commits[streamAlignedTeamKey].rate;
              return streamAlignedTeamSum;
            }, 0);
          sum.sum += sum[streamAlignedTeamKey];
          return sum;
        },
        { sum: 0 } as { [key: StreamAlignedTeam['key']]: number; sum: number }
      )
    );
  }, [master.streamAlignedTeams, organization]);

  return (
    <div
      className={classnames('grid', 'grid-flow-row', 'gap-24', props.className)}
    >
      <div className={classnames('shadow-md', 'p-4')}>
        {!teamCommits ? null : (
          <Tooltip
            texts={Object.keys(streamAlignedTeams).map(
              (streamAlignedTeamKey) =>
                `${streamAlignedTeams[streamAlignedTeamKey].name}: ${Math.floor(
                  ((teamCommits[streamAlignedTeamKey] || 1) /
                    (teamCommits.sum || 0)) *
                    100
                )}%`
            )}
          >
            <ul
              className={classnames('flex', 'rounded-full', 'overflow-hidden')}
            >
              {Object.keys(streamAlignedTeams).map((streamAlignedTeamKey) => (
                <li
                  className={classnames('h-4')}
                  style={{
                    background: streamAlignedTeams[streamAlignedTeamKey].color,
                    width: `${
                      ((teamCommits[streamAlignedTeamKey] || 1) /
                        (teamCommits.sum || 0)) *
                      100
                    }%`,
                  }}
                ></li>
              ))}
            </ul>
          </Tooltip>
        )}
      </div>
      {Object.keys(streamAlignedTeams).map((streamAlignedTeamKey) => {
        const team = streamAlignedTeams[streamAlignedTeamKey];
        const teamMembers = organization[streamAlignedTeamKey];
        return (
          <section
            className={classnames('grid', 'grid-flow-row', props.className)}
          >
            <h2
              className={classnames(
                'font-extrabold',
                'text-3xl',
                'text-center',
                'flex',
                'justify-self-center',
                'justify-center',
                'p-2'
              )}
              style={{
                color: team.color,
              }}
              key={`${streamAlignedTeamKey}-name`}
            >
              {team.name}
            </h2>
            <main key={`${streamAlignedTeamKey}-body`} className='gap-10'>
              <ul
                className={classnames('grid', 'grid-flow-row', 'gap-4', 'py-4')}
              >
                {Object.keys(teamMembers)
                  .filter((t) => teamMembers[t].length)
                  .map((tagKey) => (
                    <li
                      className={classnames('flex', 'gap-4', 'justify-center')}
                      key={`${streamAlignedTeamKey}_${tagKey}`}
                    >
                      <ul
                        className={classnames(
                          'flex',
                          'w-full',
                          'gap-4',
                          'justify-center',
                          'flex-wrap'
                        )}
                      >
                        {teamMembers[tagKey].map((member) => (
                          <li
                            className={classnames(
                              'flex',
                              'justify-center',
                              'w-full',
                              'max-w-xs',
                              'p-4',
                              'shadow-sm',
                              'hover:shadow-md'
                            )}
                            key={`${streamAlignedTeamKey}_${tagKey}_${member.name}`}
                          >
                            <Member member={member}></Member>
                          </li>
                        ))}
                      </ul>
                    </li>
                  ))}
              </ul>
            </main>
          </section>
        );
      })}
    </div>
  );
};
